<template>
  <div>
    <div class="uk-card uk-card-default">
        <div class="uk-card-media-top">
          <img :src="'../assets/' + photo" alt="">
        </div>
        <div class="uk-card-body">
          <h3 class="uk-card-title">{{title}}</h3>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TileImageDown',
  props: {
    title: String,
    text: String,
    photo: String,
  },
};
</script>



